import Dyslexia from '../modules/dyslexia';
import TCA from '../modules/TCA';
import { InfoMetadata, Story } from '../typings';

type ModuleData = {
    disclaimer: string,
    name: string,
    experienceComponent: React.ReactNode,
    informations: InfoMetadata;
    stories: Story[];
};

// eslint-disable-next-line @typescript-eslint/no-unused-vars
export default function useExperience(disabilityName: string): ModuleData {
    const component = {
        disclaimer:'Attention, les images qui vont suivre vont vous brler la rtine!',
        name: disabilityName,
        experienceComponent: <></>,
        informations: {
            description : 'Forme la plus frquente de la dyschromatopsie (trouble de la vision des couleurs), le daltonisme est une anomalie de la vue qui se caractrise par l\'absence de perception des couleurs, ou par une incapacit  diffrencier certaines teintes ou couleurs il existe 3 types : Protanopie : absence des rcepteurs rtinaux du rouge ; cette couleur est indtectable par le sujet. Protanomalie : prsence d\'une mutation du pigment de la vision du rouge ; la sensibilit  cette couleur est diminue. Tritanopie : absence des rcepteurs rtinaux du bleu ; cette couleur est indtectable par le sujet.',
            summary: ['Trouble de la vision', 'Difficult  percevoir les couleurs']
        },
        stories: [
            {
                name: 'Jrme',
                content: `Je vous cris car pour ma part, je distingue trs nettement les couleurs. Toutefois, j'ai
                    une petite faiblesse avec le vert clair que je confond parfois avec le gris clair.Aussi lors
                    que je passe les tests, je ne peux nullement voir, ou quasiment pas voir les chiffres ou
                    symboles se trouvant dans les cercles. Je ne vois que des ronds de diffrentes
                    couleurs. Aussi je sais que je suis daltonien. Je ne sais pas s'il existe rellement
                    plusieurs degrs de daltonisme, mais en tout les cas, je viens d'tre recal  une
                    embauche  cause de a. (contrleur SNCF).
                    J'ai parfois l'impression d'tre dingue. Car voir clairement toutes les couleurs et ne pas
                    russir c'est tests, je trouve cela trs frustrants`,
                type: disabilityName
            },
            {
                name: 'Jrme',
                content: `Je vous cris car pour ma part, je distingue trs nettement les couleurs. Toutefois, j'ai
                    une petite faiblesse avec le vert clair que je confond parfois avec le gris clair.Aussi lors
                    que je passe les tests, je ne peux nullement voir, ou quasiment pas voir les chiffres ou
                    symboles se trouvant dans les cercles. Je ne vois que des ronds de diffrentes
                    couleurs. Aussi je sais que je suis daltonien. Je ne sais pas s'il existe rellement
                    plusieurs degrs de daltonisme, mais en tout les cas, je viens d'tre recal  une
                    embauche  cause de a. (contrleur SNCF).
                    J'ai parfois l'impression d'tre dingue. Car voir clairement toutes les couleurs et ne pas
                    russir c'est tests, je trouve cela trs frustrants`,
                type: disabilityName
            }
        ]
    };

    switch(disabilityName) {
        case 'tca':
            component.experienceComponent = <TCA />;
            break;
        case 'dyslexia':
            component.experienceComponent = <Dyslexia />;
            break;
        default:
            throw new Error('Unknown experience');
    }

    return component;
}
